<template>
	<view class="">
		<view class="">
		天气定位：{{address}}		
	</view>
	<view class="">		
		地理定位：{{addressName}}
	</view>
	</view>
	
</template>

<script>
	export default {
		data() {
			return {
				address:'',
				addressName:''
			}
		},
		onLoad() {
			var that = this;
			var amapFile = require('../../common/amap-wx.js');
			var amapPlugin = new amapFile.AMapWX({
				key: "e8b6f69a02c2812e7def0dceccedf35b"
			});
			amapPlugin.getWeather({
				success: (res) => {
					this.address=res.city.data
					//成功回调
					// console.log(res)
				},
				fail: function(info) {
					//失败回调
					console.log(info)
				}
			});
			amapPlugin.getRegeo({
				success: data => {
					this.addressName = data[0].name;
				}
			});
		}
	}
</script>

